.dialog {
  overflow: unset;
  width: 80%;
  margin: auto;
  position: relative; }
  .dialog .paperViable {
    overflow: visible; }
  .dialog .dialogWrapper {
    width: 100%;
    height: 100%;
    position: relative; }
  .dialog .dialogFloatButton {
    position: absolute;
    z-index: 99;
    right: 0;
    top: 0;
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
    -webkit-transform: translate3d(50%, -50%, 0);
            transform: translate3d(50%, -50%, 0); }
  .dialog .dialogContent {
    min-height: 300px;
    width: 80%;
    margin: auto;
    padding: 1rem; }
    .dialog .dialogContent .input {
      width: 100%; }
    .dialog .dialogContent .feedInputContainer {
      position: relative; }
    .dialog .dialogContent .feedButton {
      min-width: unset;
      min-height: unset;
      padding: .2rem;
      position: absolute;
      right: 0; }



